                 margin-right: 5px;\n                        }\n\n                        span {\n                            color: $text-color;\n                            font-size: 10px;\n                            font-weight: 500;\n                            text-transform: uppercase;\n                        }\n                    }\n\n                    i {\n                        font-size: 18px;\n                    }\n                }\n\n                .ivu-badge {\n                    position: relative;\n\n                    a {\n                        display: block;\n                        max-height: 32px;\n                        width: 32px;;\n                        font-size: 11px;\n                        cursor: pointer;\n                        font-weight: 500;\n                        transition: all .2s ease-in-out;\n                        text-align: center;\n                        padding: 0;\n\n                        i,\n                        span {\n                            color: $text-color;\n                            line-height: 32px;\n                        }\n\n                        &:hover {\n                            i {\n                                color: $primary;\n                            }\n                        }\n                    }\n\n                    sup {\n                        top: 5px;\n                        right: 5px;\n                        box-shadow: none;\n                    }\n                }\n            }\n        }\n\n        > li {\n            position: relative;\n            float: left;\n\n            > a,\n            .ivu-poptip-rel > a,\n            .ivu-badge > a {\n                height: 42px;\n                min-width: 42px;\n                display: flex;\n                align-items: center;\n                justify-content: center;\n                text-align: center;\n\n                &.transparent {\n                    background: rgba(#ffffff, .7);\n                    margin-right: 5px !important;\n                }\n\n                i {\n                    color: $text-color;\n                    line-height: 42px;\n                    font-size: 22px;\n                }\n\n                &:hover {\n                    i,\n                    span {\n                        color: $primary;\n                    }\n                }\n\n                &.avatar {\n                    padding: 0;\n\n                    img {\n                        max-height: 32px;\n                        max-width: 32px;;\n                        border-radius: 50%;\n                        margin: 0 auto;\n                    }\n\n                    span {\n                        color: $text-color;\n                        font-size: 12px;\n                        font-weight: 500;\n                    }\n                }\n            }\n\n            &:last-child {\n                border-right: 0;\n            }\n\n            .ivu-badge {\n                a {\n                    display: inline-block;\n                    position: relative;\n                    height: 42px;\n                    min-width: 32px;\n                    font-size: 14px;\n                    cursor: pointer;\n                    font-weight: 500;\n                    transition: all .2s ease-in-out;\n                    text-transform: uppercase;\n\n                    i,\n                    span {\n                        color: $text-color;\n                        line-height: 42px;\n                    }\n\n                    &:hover {\n                        i {\n                            color: $primary;\n                        }\n                    }\n                }\n\n                sup {\n                    top: 10px;\n                    right: 10px;\n                    box-shadow: none;\n                }\n            }\n\n            .header-apps {\n                width: 230px;\n                padding: 0 0 10px 0;\n\n                .ivu-col {\n                    padding: 10px 0;\n\n                    a {\n                        display: flex;\n                        flex-direction: column;\n                        border-radius: 3px;\n                        height: 90px;\n                        width: 75px;\n                        color: #737373;\n                        align-items: center;\n                        justify-content: center;\n                        border: 1px solid transparent;\n\n                        &:hover {\n                            border: 1px solid #eeeeee;\n                        }\n\n                        img {\n                            height: 35px;\n                            max-height: 35px;\n                            max-width: 35px;\n                            margin-bottom: 8px;\n                        }\n\n                        span {\n                            display: block;\n                            overflow: hidden;\n                            text-overflow: ellipsis;\n                            white-space: nowrap;\n                            font-size: 12px;\n                            color: #555555;\n                        }\n                    }\n                }\n            }\n\n            &.avatar-item {\n                .ivu-poptip-content {\n                    padding: 0;\n\n                    .ivu-poptip-arrow {\n                        border-bottom-color: $primary;\n\n                        &:after {\n                            border-bottom-color: $primary;\n                        }\n                    }\n\n                    .ivu-poptip-body {\n                        padding: 0 !important;\n                        width: 200px;\n\n                        .header-profile {\n                            padding: 0 0 5px;\n\n                            &-info {\n                                background: $primary;\n                                padding: 8px 16px;\n                                border-top-left-radius: 3px;\n                                border-top-right-radius: 3px;\n                                color: #ffffff;\n                                margin-bottom: 10px;\n\n                                h3 {\n                                    font-size: 18px;\n                                }\n\n                                small {\n                                    font-size: 11px;\n                                }\n                            }\n\n                            ul {\n                                li {\n                                    a {\n                                        display: flex;\n                                        align-items: center;\n                                        padding: 0 16px;\n                                        color: #555555;\n                                        width: 200px;\n                                        justify-content: left;\n                                        cursor: pointer;\n\n                                        i {\n                                            width: 20px;\n                                            margin-right: 8px;\n                                        }\n\n                                        &:hover {\n                                            background: #f4f5f6;\n                                        }\n                                    }\n                                }\n                            }\n                        }\n                    }\n                }\n            }\n        }\n    }\n}\n"],"sourceRoot":""}]);
